import React, { useState } from 'react'
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  IconButton,
  Tooltip,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'
import { PlaylistAdd } from '@material-ui/icons'

const AddToQueueDialog = ({ balance, cost, currentUser, onSubmit }) => {
  const [open, setOpen] = useState(false)

  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'))

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleSubmit = () => {
    onSubmit()
    setOpen(false)
  }

  return (
    <>
      <Tooltip title='Add to queue' arrow>
        <IconButton onClick={handleClickOpen}>
          <PlaylistAdd />
        </IconButton>
      </Tooltip>
      <Dialog
        fullScreen={fullScreen}
        fullWidth
        maxWidth='md'
        onClick={(ev) => ev.stopPropagation()}
        onClose={handleClose}
        onFocus={(ev) => ev.stopPropagation()}
        open={open}
      >
        <DialogTitle>Add to Queue</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Would you like to add this song to the Spotify queue to play next
            for a fee of {cost} pts?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          {balance >= cost || currentUser.role !== 'student' ? (
            <Button
              autoFocus
              color='primary'
              onClick={handleSubmit}
              variant='contained'
            >
              Purchase
            </Button>
          ) : (
            <Tooltip
              title={`Insufficient funds! You have ${balance} pts`}
              arrow
            >
              <span>
                <Button color='primary' disabled variant='contained'>
                  Puchase
                </Button>
              </span>
            </Tooltip>
          )}
          <Button onClick={handleClose} variant='contained'>
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </>
  )
}

export default AddToQueueDialog
